/*$primary: #5BCAA4;*/
.navbar {
    position: absolute;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    width: 100%;
    padding: 32px 118px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    margin-top: 18px;
}
.navbar .logo-container .navbar-logo {
            height: 100px;
        }
.navbar .menu {
        display: -ms-flexbox;
        display: flex;
        height: 48px;
    }
.navbar .menu .menu-item {
            display: -ms-flexbox;
            display: flex;
            -ms-flex-pack: center;
                justify-content: center;
            margin: 0 14px;
            border: 2px solid transparent;
            border-radius: 4px;
            -webkit-transition: all 0.2s;
            -o-transition: all 0.2s;
            transition: all 0.2s;
        }
.navbar .menu .menu-item a {
                position: relative;
                padding: 16px;
                display: -ms-flexbox;
                display: flex;
                -ms-flex-pack: center;
                    justify-content: center;
                text-transform: uppercase;
                text-decoration: none;
                font-weight: 800;
                letter-spacing: 2px;
                color: #373435;
            }
.navbar .menu .menu-item a .rightIcon {
                    position: absolute;
                    top: calc(50% + 2px);
                    right: 4px;
                    -webkit-transform: translateY(-50%);
                        -ms-transform: translateY(-50%);
                            transform: translateY(-50%);
                }
.navbar .menu .menu-item .withIcon {
                padding-right: 32px;
            }
.navbar .menu .menu-item:hover {
    cursor: pointer;
    border: 2px solid rgb(44, 42, 42);
}
.navbar .menu .menu-item:hover a { color: rgb(44, 42, 42);
}
.navbar .menu .rentals {
            background: #373435;
            border: 2px solid #373435;
        }
.navbar .menu .rentals a { color: #fff; }
.navbar .menu .rentals:hover {
    background: rgb(44, 42, 42);
    border: 2px solid rgb(44, 42, 42);
}
.navbar .menu .rentals:hover a { color: #fff;
}
.navbar .menu .sales {
            border: 2px solid #373435;
        }
.navbar .menu .sales a { color: #373435; }
.navbar .menu .sales:hover {
    background: rgb(44, 42, 42);
    border: 2px solid rgb(44, 42, 42);
}
.navbar .menu .sales:hover a { color: #fff;
}
@media (max-width: 1300px) {
    .navbar {
        padding-left: 32px;
        padding-right: 32px;
    }
}
@media (max-width: 1100px) {
                .navbar .menu .menu-item a {
                    font-size: 12px;
                }
            .navbar .menu .home {
                display: none;
            }
}
@media (max-width: 880px) {
            .navbar .menu .about {
                display: none;
            }
}
@media (max-width: 700px) {
            .navbar .menu .gibraltar {
                display: none;
            }
}
@media (max-width: 540px) {
            .navbar .menu .sales, .navbar .menu .rentals {
                display: none;
            }
}
@media (max-width: 300px) {
        .navbar .menu {
            display: none;
        }
}


