/*$primary: #5BCAA4;*/
.home {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
}
.home .hero {
        height: calc(100vh - 128px);
        width: 100%;
        background: -webkit-gradient(linear, left top, left bottom, from(rgba(255,255,255, 0.9)), to(transparent)), url('./background.jpeg') no-repeat center;
        background: -webkit-linear-gradient(rgba(255,255,255, 0.9), transparent), url('./background.jpeg') no-repeat center;
        background: -o-linear-gradient(rgba(255,255,255, 0.9), transparent), url('./background.jpeg') no-repeat center;
        background: linear-gradient(rgba(255,255,255, 0.9), transparent), url('./background.jpeg') no-repeat center;
        background-size: cover;
    }
.home .hero .content {
            position: absolute;
            top: 50%;
            left: 50%;
            -webkit-transform: translate(-50%, -50%);
                -ms-transform: translate(-50%, -50%);
                    transform: translate(-50%, -50%);
            display: -ms-flexbox;
            display: flex;
            -ms-flex-direction: column;
                flex-direction: column;
            -ms-flex-pack: center;
                justify-content: center;
            -ms-flex-align: center;
                align-items: center;
        }
.home .hero .content .title {
                font-size: 58px;
                color: #373435;
                font-family: 'Raleway', roboto, sans-serif;
                font-weight: 600;
                margin-bottom: 40px;
                text-shadow: 2px 4px 3px rgba(0,0,0,0.1);
            }
.home section {
        padding-top: 40px;
    }
@media (max-width: 1300px) {
    .content {
        width: 80%;
    }
        .content .title {
            text-align: center;
        }
}
@media (max-width: 900px) {
                .home .hero .content .title {
                    font-size: 42px;
                }
}
@media (max-width: 645px) {
                .home .hero .content .title {
                    font-size: 38px;
                }
}




